<style lang="scss" scoped>
.vision-list {
	margin-bottom: 1rem;
	
	.vision {
		margin-bottom: 0.4rem;
		
		.column {
			padding: 0;
		}
	}
}
</style>

<template>
	<div v-if="pc">
		<div class="vision-list">
			
			<div class="vision columns" v-for="(v, vi) in pc.visions">
				
				<!-- Vision type -->
				<div class="column">
					<label class="label is-small">{{ v.name }}</label>
					<div class="field">
						<div class="control is-expanded">
							<div class="select is-small">
								<select v-model="v.type" @change="triggerUpdate">
									<option value="" disabled>Select type</option>
									<option v-for="v in visionTypes" :value="v">{{ v }}</option>
								</select>
							</div>
						</div>
					</div>
				</div>
				
				<div class="column">
					<label class="label is-small">Channels</label>
					<div class="field">
						<!-- Vision channels -->
						<div class="control">
							<input type="text"
								class="input is-small"
								placeholder="Channels"
								v-model="v.channels"
								@change="channelsChanged(v)"
							>
						</div>
					</div>
				</div>
				
				<div class="column">
					<!-- Camera qty -->
					<label class="label is-small">Cam Qty</label>
					<div class="field">
						<div class="control">
							<input type="text"
								class="input is-small"
								placeholder="Cam Qty"
								v-model="v.camQty"
								@change="camChanged(v)"
							>
						</div>
					</div>
				</div>
				
				<div class="column">
					<!-- Cable override -->
					 <label class="label is-small">TRG</label>
					 <div class="field">
						<div class="control">
							<select class="input is-small" v-model="v.camIndex" @change="camChanged(v)">
								<option :value="null">-</option>
								<option v-for="i in 20" :value="i - 1">C{{ i + 10 }}</option>
							</select>
						</div>
					 </div>
				</div>
				
				<div class="column">
					<!-- Delete button -->
					<label class="label is-small">&nbsp;</label>
					<div class="field">
						<div class="control">
							<button class="button is-danger is-small" @click="removeVision(vi)">
								<icon :size="16" :data="icons.mdiDelete" />
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
		
		<!-- Vision actions -->
		<div class="buttons">
			<!-- Add new vision -->
			<button class="button is-primary" @click="addVision">
				<icon :size="16" :data="icons.mdiPlus" />
				<span>New Vision</span>
			</button>
			
			<!-- Remove all visions -->
			<button class="button is-warning" @click="clear">
				<icon :size="16" :data="icons.mdiClose" />
				<span>Clear</span>
			</button>
		</div>
		
	</div>
</template>
<script>
import { mdiPlus, mdiDelete, mdiClose } from '@mdi/js'
import visions from './visions'

export default {
	props: [ 'pc' ],
	data() {
		return {
			NUM_CHANNELS: 8,
			icons: {
				mdiPlus,
				mdiDelete,
				mdiClose
			},
			visionTypes: visions
		};
	},
	methods: {
		addVision() {
			this.pc.visions.push({
				name: `Vision ${this.pc.visions.length + 1}`,
				type: '',
				channels: 1,
				camQty: 1,
				camLabel: '',
				camIndex: null,
			});
		},
		removeVision(vi) {
			this.pc.visions.splice(vi, 1);
			this.triggerUpdate();
		},
		clear() { this.pc.visions = []; },
		
		channelsChanged(v) {
			if (v.channels > this.NUM_CHANNELS) v.channels = 8;
			this.triggerUpdate();
		},
		camChanged(v) {
			if (v.camQty > 3) v.camQty = 3;
			this.triggerUpdate();
		},
		
		triggerUpdate() { this.$emit('update'); }
	}
}
</script>
