<style lang="scss" scoped>
.lsc-pinout {
	
	.lsc-header {
		margin-bottom: 1rem;
		.title { margin-bottom: 0.5rem; }
	}
	
	.tables {
		display: flex;
		
		.table {
			font-size: 0.7rem;
			margin-right: 1rem;
		}
		
		.divider {
			height: 1.8rem;
			border: none;
		}
	}
	
}
</style>
<template>
	<div class="lsc-pinout">
		<div class="lsc-header">
			<div class="title is-5 has-text-grey">Device Manager: COM{{ offsets.com + index }}</div>
			<div class="title is-4">LSC {{ index + 1}} (BoardID [{{ binStringReversed(index) }}])</div>
		</div>
		
		<div class="tables">
			
			<table class="table is-narrow is-bordered">
				
				<!-- Camera section -->
				<tr>
					<th colspan="4">Cameras</th>
				</tr>
				<tr>
					<th>Vision</th>
					<th>Label</th>
					<th>TRG Cable</th>
				</tr>
				<tr v-for="(v, vi) in lsc.visions">
					<th>{{ v.type }}</th>
					<td>{{ v.camLabel }}</td>
					<td>{{ v.cams.join('/') }}</td>
					<!--<td>C{{ offsets.vision + lsc.visionStart + vi }}</td>-->
					<!--<td>CN{{ offsets.connector + lsc.connectorStart + vi + 4 }}</td>-->
				</tr>
				
				<!-- Divider -->
				<tr><td class="divider" colspan="4"></td></tr>
				
				<tr>
					<th colspan="4">Connectors</th>
				</tr>
				<!-- PWR -->
				<tr>
					<th>PWR</th>
					<td colspan="3">CN{{ pwrConnector }}</td>
				</tr>
				<!-- USB -->
				<tr>
					<th>USB</th>
					<td colspan="3">CN{{ usbConnector }}</td>
				</tr>
				<!-- Ext Pwr -->
				<tr>
					<th>EXT PWR</th>
					<td colspan="3">CN{{ extPwrConnector }}</td>
				</tr>
			</table>
			
			<table class="table is-narrow is-bordered">
				<tr><th colspan="3">Channels</th></tr>
				<tr>
					<th>Physical</th>
					<th>Label</th>
					<th>Vision</th>
				</tr>
				
				
				<tr v-for="(ch, i) in lscChannels">
					<th>CH{{ i + 1 }}</th>
					<td>CH{{ offsets.channel + lsc.channelStart + i + 1 }}</td>
					<td>{{ lscChannels[i] }}</td>
				</tr>
				
			</table>
			
			<table class="table is-narrow is-bordered">
				<tr><th colspan="2">TRG Cable Input/Output Pins</th></tr>
				<tr v-for="(port, i) in lscPorts">
					<th>RST{{ i + 1 }} (ST-X) | TRG{{ i + 1 }} (ST-Y) | COM{{ i + 1 }} (GND)</th>
					<td>{{ port }}</td>
				</tr>
			</table>
		</div>
		
		
	</div>
</template>
<script>
export default {
	props: [ 'lsc', 'index', 'offsets' ],
	computed: {
		
		pwrConnector() { return this.offsets.connector + this.lsc.connectorStart + 1 },
		usbConnector() { return this.offsets.connector + this.lsc.connectorStart + 2 },
		extPwrConnector() { return this.offsets.connector + this.lsc.connectorStart + 3 },
		
		
		lscChannels() {
			if (!this.lsc) return [];
			
			var channels = [];
			for (var i in this.lsc.visions) {
				var vision = this.lsc.visions[i];
				for (var j = 0; j < vision.channels; ++j) {
					channels.push(`${vision.type}-Ch${j + 1}`)
				}
			}
			while (channels.length < this.lsc.numChannels) channels.push('-');
			
			return channels;
		},
		lscPorts() {
			if (!this.lsc) return [];
			var ports = [];
			for (var i = 0; i < this.lsc.numChannels; ++i) ports.push('-');
			
			var pi = 0;
			for (var i in this.lsc.visions) {
				ports[pi] = this.lsc.visions[i].type;
				pi += this.lsc.visions[i].channels;
			}
			return ports;
		}
		
	},
	methods: {
		binStringReversed(val) {
			var bin = val.toString(2);
			while (bin.length < 4) bin = '0' + bin;
			return bin.split('').reverse().join('');
		}
	}
	
}
</script>
