<style lang="scss" scoped>
#app {
	height: 100vh;
	display: flex;
	flex-direction: column;
	
	.tabs {
		margin-bottom: 0;
		flex-shrink: 0;
		
		.delete { margin-left: 0.5rem; }
		
		@media print { display: none; }
	}
	
	.app-content {
		flex-grow: 1;
		overflow-y: auto;
		
		
		& > .column {
			height: 100%;
			overflow-y: auto;
		}
		
		.sidebar {
			flex-grow: 0;
			flex-basis: 20%;
			
			@media print { display: none; }
		}
		
		.lsc-tables {
			.column {
				flex-basis: 50%;
				flex-shrink: 0;
			}
		}
	}
}
</style>

<template>
	<div id="app">
		
		
		<!-- PC Tabs -->
		<div class="tabs">
			<ul>
				<!-- PC Tabs -->
				<li v-for="(pc, index) in pcList"
					@click="selectPC(index)"
					:class="{ 'is-active': index == activePCIndex }"
				>
					<a>
						<span>PC {{ index + 1 }}</span>
						<span class="delete is-small" @click="deletePC(index)"></span>
					</a>
				</li>
				
				<!-- Create New PC tab -->
				<li>
					<a @click="createNewPC">
						<icon :data="icons.mdiPlus" />
						<span>Add New</span>
					</a>
				</li>
			</ul>
		</div>
		
		<div class="app-content columns">
			
			
			<template v-if="activePC">
				
				<div class="sidebar column">
					<div class="field">
						<label class="label">Starting COM</label>
						<div class="control">
							<div class="com select is-small">
								<select v-model="startingCOM" @change="update">
									<option value="2">COM 2</option>
									<option value="3">COM 3</option>
									<option value="4">COM 4</option>
									<option value="5">COM 5</option>
									<option value="6">COM 6</option>
									<option value="7">COM 7</option>
									<option value="8">COM 8</option>
									<option value="9">COM 9</option>
								</select>
							</div>
						</div>
					</div>
					
					<!-- Vision list -->
					<vision-list @update="update" :pc="activePC" />
				</div>
				
				<div class="column">
					<!-- Diagram for reference -->
					<lsc-diagram />
					
					<!-- Pinout tables -->
					<lsc-pinout v-for="(lsc, index) in lscList" :lsc="lsc" :index="index" :offsets="pcOffsets" />
					
					
				</div>
			</template>
			
			
		</div>
		
	</div>
</template>

<script>
import { mdiPlus, mdiRefresh } from '@mdi/js'
import VisionList from '@/views/VisionList'
import LscPinout from '@/views/LSCPinout'
import LscDiagram from '@/views/LSCDiagram'

export default {
	components: {
		VisionList,
		LscPinout,
		LscDiagram
	},
	computed: {
		pcStartingCOM() {
			var numInFront = 0;
			for (var i = 0; i < this.activePCIndex; ++i) {
				numInFront += this.pcList[i].numCOMUsed * 1;
			}
			return (this.startingCOM * 1) + numInFront;
		},
		pcOffsets() {
			var lscOffset = 0;
			var channelOffset = 0;
			var visionOffset = 0;
			var connectorOffset = 0;
			
			for (var i = 0; i < this.activePCIndex; ++i) {
				var pc = this.pcList[i];
				
				// Number of things before this PC
				lscOffset += pc.numLSC;
				channelOffset += pc.totalChannels;
				visionOffset += pc.numVisions;
				connectorOffset += pc.numConnectors;
			}
			
			
			return {
				com: this.startingCOM * 1 + lscOffset,
				lsc: lscOffset,
				channel: channelOffset,
				vision: visionOffset,
				connector: connectorOffset
			}
		}
	},
	watch: {
		activePC(n) { this.update(); },
	},
	data() {
		return {
			NUM_CHANNELS: 8,
			STARTING_CAM_INDEX: 11,
			
			icons: {
				mdiPlus,
				mdiRefresh
			},
			activePC: null,
			activePCIndex: 0,
			startingCOM: 2,
			pcList: [],
			lscList: []
		};
	},
	methods: {
		createNewPC() {
			this.pcList.push({
				numCOMUsed: 0,
				visions: []
			});
			this.selectPC(this.pcList.length - 1);
		},
		selectPC(index) {
			if (index < 0 || index >= this.pcList.length) {
				this.activePC = null;
				this.activePCIndex = -1;
				return;
			}
			
			this.activePC = this.pcList[index]; 
			this.activePCIndex = index;
			
			this.update();
		},
		deletePC(index) {
			this.pcList.splice(index, 1);
			
			if (this.pcList.length > 0) {
				this.selectPC(index - 1);
			} else {
				this.activePC = null;
				this.activePCIndex = -1;
			}
			this.update();
		},
		
		startingCamIndex() {
			let camIndex = this.STARTING_CAM_INDEX;
			for (let i = 0; i < this.activePCIndex; ++i) {
				const visions = this.pcList[i].visions;
				for (let j = 0; j < visions.length; ++j) {
					camIndex += parseInt(visions[j].camQty);
				}
			}
			return camIndex;
		},
		
		update() {
			this.lscList = [];
			if (!this.activePC) return;
			
			
			var visions = this.activePC.visions.filter(v => {
				if (v.type == '') return false;
				if (!v.channels) return false;
				return true;
			});
			
			
			// Sort visions by numChannels
			visions.sort((a, b) => { return b.channels - a.channels });
			
			// Fit visions into lscList
			let totalChannels = 0;
			let camIndex = this.startingCamIndex();
			const camOverrides = {};
			for (var i in visions) {
				var v = visions[i];
				
				var cams = [];
				for (var j = 0; j < v.camQty; ++j) {
					if (v.camIndex !== null) {
						if (camOverrides[v.camIndex] == undefined) camOverrides[v.camIndex] = this.STARTING_CAM_INDEX;
						cams.push(`C${v.camIndex + camOverrides[v.camIndex]++}`);
						++camIndex;
					} else {
						cams.push(`C${camIndex}`);
						++camIndex;
					}
				}
				v.cams = cams;
				v.camLabel = `${v.type} ${cams.join('/')}`;
				
				
				// Try to fit it into an existing LSC
				var fit = false;
				for (var j in this.lscList) {
					var lsc = this.lscList[j];
					
					
					// A suitable slot is found, put it in
					if (lsc.numChannels - lsc.numUsed >= v.channels) {
						this.attachToLSC(v, lsc);
						fit = true;
						break;
					}
				}
				if (!fit) {
					// It doesn't fit, create a new LSC and attach to it
					var lsc = this.createLSC();
					this.attachToLSC(v, lsc);
					this.lscList.push(lsc);
					
					// Accumulate channel count if new LSC was created
					totalChannels += lsc.numChannels;
				}
			}
			
			// Splice the LSC containing the first vision and put it as the first
			var firstVision = this.activePC.visions[0];
			for (var i in this.lscList) {
				var lsc = this.lscList[i];
				for (var j in lsc.visions) {
					var vis = lsc.visions[j];
					if (vis.type == firstVision.type && vis.channels == firstVision.channels) {
						var firstLSC = this.lscList.splice(i, 1);
						this.lscList.unshift(firstLSC[0]);
						break;
					}
				}
			}
			
			
			// Calculate starting channel for each LSC
			var channelStart = 0;
			var visionStart = 11;
			var connectorStart = 0;
			for (var i in this.lscList) {
				var lsc = this.lscList[i];
				lsc.channelStart = channelStart;
				lsc.visionStart = visionStart;
				lsc.connectorStart = connectorStart;
				
				channelStart += lsc.numChannels;
				visionStart += lsc.visions.length;
				connectorStart += lsc.visions.length + 3;		// 3 for pwr, usb and extPwr
			}
			
			
			this.activePC.numLSC = this.lscList.length;
			this.activePC.numVisions = visions.length;
			this.activePC.totalChannels = totalChannels;
			this.activePC.numConnectors = connectorStart;
		},
		
		createLSC() {
			var lsc = {
				numChannels: this.NUM_CHANNELS,
				numUsed: 0,
				visions: [],
				ports: []
			};
			for (var i = 0; i < lsc.numChannels; ++i) {
				lsc.ports.push('-');
			}
			
			return lsc;
		},
		
		
		
		attachToLSC(vision, lsc) {
			vision.channels *= 1;
			lsc.visions.push(vision);
			
			lsc.numUsed += vision.channels;
		}
		
	}
}
</script>
